.loader-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #f9f7f7;

  .loader {
    width: 120px;
    height: 75px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: space-between;

    span {
      font-size: 22px;
      text-transform: uppercase;
      margin: auto;
    }

    .ball {
      width: 25px;
      height: 25px;
      border-radius: 50%;
      background-color: #17949d;
      animation: bounce 0.5s alternate infinite;
    }

    .ball:nth-child(2) {
      animation-delay: 0.16s;
    }

    .ball:nth-child(3) {
      animation-delay: 0.32s;
    }

    @keyframes bounce {
      from {
        transform: scaleX(1.25px);
      }
      to {
        transform: translateY(-50px) scaleX(1);
      }
    }
  }
}

.nodata-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 100%;
  background-color: #f9f7f7;
}

.nodata-screen-sms {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 100%;
  background-color: #fff;
}
