#sms-register {
  input,
  select {
    background-color: #fff;
  }
}

#sms-leads-detail {
  p {
    margin-bottom: 8px;
  }
}

table {
  border-collapse: collapse;
  /* border-radius: 1em 1em 0 0; */
  overflow: hidden;
  width: 100%;
  margin: 15px 0;

  th {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 21px;
    /* identical to box height */
    color: #ffffff;
    background: #17949d;
  }

  @media screen and (max-width: 768px) {
    th,
    td {
      padding: 8px !important;
      font-size: 10px !important;
    }
  }

  tr:nth-child(even) {
    background-color: #eaeaea;
  }

  td {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 21px;
    /* identical to box height */
    color: #313132;
    border-bottom: 1px solid #c4c4c4;

    a {
      text-decoration: none;
      color: #17949d;
    }
  }

  th,
  td {
    padding: 1em;
    /*   background: #ddd; */
    /*   border-bottom: 2px solid #000;  */
  }

  thead th:last-child {
    border-radius: 0 8px 8px 0;
  }

  thead th:first-child {
    border-radius: 8px 0 0 8px;
  }
}

.alert {
  padding: 6px 12px !important;
  margin-top: 5px;
}
