.sms-sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  width: 140px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  overflow: auto;
  background: linear-gradient(180deg, #17949d 0%, #21b8c3 100%);
  

  .sidebar-logo {
    img {
      width: 100%;
      padding: 30px 24px;
    }
  }

  .nav-link {
    width: 140px;
    padding: 20px 35px;
    font-style: normal;
    font-weight: 400 !important;
    font-size: 12px;
    line-height: 21px;
    color: #ffffff !important;
    text-align: center;

    img {
      width: 25px;
      margin-right: 10px;
    }
  }

  .active {
    transition-delay: 1s;
    border-right: solid #17949d;
    color: #2470dc;
    background-color: #eaeaea;
  }

  .dropdown {
    cursor: pointer;

    .sub-menu {
      display: none;

      .nav-item {
        .nav-link {
          padding: 12px 50px;
          

          // p {
          //   margin: 0px 0px 0px 25px;
          // }
        }
      }
    }

    .dd-active {
      display: block;
    }

    .arrow-bar {
      width: 14px !important;
      height: 14px;
    }
  }
}

