@media screen and (max-width: 768px) {
  #sms-leads {
    table th,
    table td {
      font-size: 12px !important;
    }

    table tfoot tr td {
      font-family: "Poppins";
      font-style: normal;
      font-weight: 700;
      font-size: 13px;
      line-height: 21px;
      color: #ffffff;
      background: #c4c4c4;
    }
  }

  #sms-add-leads {
    form {
      .alert {
        padding: 10px !important;
        font-size: 12px;
        margin-bottom: 5px;
      }

      span {
        color: red;
      }
    }
  }

  #sms-leads-detail {
    p {
      margin-bottom: 8px;
    }
  }
}
